<template>
    <div>
      <!-- <loading :active='isLoading'/> -->
      <div class="pro_bg">
        <div class="container product_list ">
          <div class="row pt-4 d-flex justify-content-between" >
            <div>
              <button class="btn-dark">
                <router-link :to="{name: 'Bsproduct'}" ></router-link>
              </button>
              <button class="btn-dark">
                <router-link :to="{name: 'Coupon'}"></router-link>
              </button>
              <button class="btn-dark">
                 <router-link :to="{name:'Orderlist'}"></router-link>
              </button>
            </div >
            <div>
              <button class="btn-dark" @click="openModal(true)"></button>
              <button class="btn-dark" @click="logout()"></button>
            </div>

          </div>

          <table class="table mt-4">
            <thead>
                <tr>
                    <th width="100"></th>
                    <th></th>
                    <th width="80"></th>
                    <th width="80"></th>
                    <th width="80"></th>
                    <th width="100"></th>
                    <th width="100"></th>
                </tr>
            </thead> 
           
           <tbody>
               <tr v-for="item in products" :key="item.id">
                   <td>{{item.category}}</td>
                   <td>{{item.title}}</td>
                   <td>{{item.original_price|currency}}</td>
                   <td>{{item.price | currency}}</td>
                   <td v-if="item.is_enabled" class="text-success"> </td>
                   <td v-else></td> 
                   <td>
                     <button class="btn btn-outline-dark btn-sm" @click="openModal(false,item)"></button>
                   </td>
                   <td>
                     <button class="btn btn-outline-dark btn-sm" @click="openDel(item)"></button>
                   </td>
               </tr>
           </tbody>

          </table>
          <pagination :page= pagination @switchPage="getProducts"></pagination>
      <!-- data paginationpage,prop -->
      <!-- @switchPagepaginationcomponentgetProduct() -->
      
        </div>
       </div>

<!--  -->
       <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
       aria-labelledby="exampleModalLabel" aria-hidden="true">
          <div class="modal-dialog modal-lg" role="document">
            <div class="modal-content border-0">
              <div class="modal-header bg-dark text-white">
                <h5 class="modal-title" id="exampleModalLabel">
                  <span></span>
                </h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                   <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div class="modal-body">
                <div class="row">
                  <div class="col-sm-4">
                    <div class="form-group">
                      <label for="image"></label>
                      <input type="text" class="form-control" id="image"
                       placeholder="" v-model="tempProduct.imageUrl">
                    </div>
                    <div class="form-group">
                      <label for="customFile"> 
                         <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                      </label>
                      <input type="file" id="customFile" class="form-control"
                     ref="files" @change="uploadFile">
                       <!-- **ref=files** -->
                    </div>
                    <img :src="tempProduct.imageUrl"
                      class="img-fluid" alt="">
                  </div>
                  <div class="col-sm-8">
                    <div class="form-group">
                      <label for="title" ></label>
                      <input type="text" class="form-control" id="title"
                        placeholder="" v-model="tempProduct.title">
                    </div>

                    <div class="form-row">
                      <div class="form-group col-md-6">
                        <label for="category"></label>
                        <input type="text" class="form-control" id="category"
                          placeholder="" v-model="tempProduct.category"> 
                      </div>
                      <div class="form-group col-md-6">
                        <label for="price"></label>
                        <input type="unit" class="form-control" id="unit"
                          placeholder="" v-model="tempProduct.unit">
                      </div>
                   </div>

                   <div class="form-row">
                    <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                      <input type="number" class="form-control" id="origin_price"
                        placeholder="" v-model="tempProduct.original_price">
                    </div>
                    <div class="form-group col-md-6">
                      <label for="price"></label>
                      <input type="number" class="form-control" id="price"
                        placeholder="" v-model="tempProduct.price">
                    </div>
                   </div>
                   <hr>

                    <div class="form-group">
                      <label for="description"></label>
                      <textarea type="text" class="form-control" id="description"
                        placeholder="" v-model="tempProduct.description"></textarea>
                    </div>
                    <div class="form-group">
                      <label for="content"></label>
                      <textarea type="text" class="form-control" id="content"
                        placeholder="" v-model="tempProduct.content"></textarea>
                    </div>
                    <div class="form-group">
                      <div class="form-check">
                        <input class="form-check-input" type="checkbox"
                          id="is_enabled" v-model="tempProduct.is_enabled"
                          :true_value="1"
                          :false_value="0">
                          <!-- data0falsetrue -->
                        <label class="form-check-label" for="is_enabled">
                          
                        </label>
                      </div>
                    </div>
                 </div>
               </div>
             </div>
              <div class="modal-footer">
                <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                <button type="button" class="btn btn-primary" @click="updateProduct()"></button>
              </div>
           </div>
         </div>
  
      </div>

       <!--  -->
      <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
        aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog" role="document">
          <div class="modal-content border-0">
            <div class="modal-header bg-danger text-white">
              <h5 class="modal-title" id="exampleModalLabel">
                <span></span>
              </h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
               <strong class="text-danger"> {{tempProduct.title }}</strong> ()
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-outline-secondary" data-dismiss="modal(item)"></button>
              <button type="button" class="btn btn-danger" @click="deleteProduct"></button>
            </div>
          </div>
        </div>

      </div>



    </div>
</template>


<script>
import pagination from "../../pagination"
import {getBsProductsAPI,logoutAPI,addBsProductAPI,updateBsProductAPI,deleteBsProductAPI} from "../../../api/api"
export default {
    components:{
      pagination
    },
    data() {
        return {
            products:[],
            tempProduct:{},
            //API
            isNew:false,
            //
            pagination:{},
            // isLoading:false,
            status:{
              fileUploading:false,
            }

        }
    },
    methods: {
      getProducts(page){
        getBsProductsAPI(page).then((res)=>{
          if(res.data.success){
            this.products=res.data.products;
            this.pagination=res.data.pagination;
          }else{
            console.log(res)
          }
        })
        .catch(err=>console.log(err))

      },
    logout(){
       logoutAPI().then((res)=>{
         if(res.data.success){
          this.$router.push("/admin/login")
         }
       })
       .catch(err=>console.log(err))
    },

    openModal(isNew,item){
      //
      if(isNew){
        this.tempProduct={},
        this.isNew=true

      }else{
        this.tempProduct=Object.assign({},item);
          //ES6item
      }
      $("#productModal").modal("show");
    },

    openDel(item){
      this.tempProduct=Object.assign({},item);
      $("#delProductModal").modal("show")
    },

    updateProduct(){
      if(this.isNew){
        addBsProductAPI({data:this.tempProduct}).then((res)=>{
          if (res.data.success){
             $("#productModal").modal("hide");
             this.getProducts();
          }else{
            //TODO
             console.log("")
          }
        })
        .catch(err=>console.log(err))
      }else{
        updateBsProductAPI(this.tempProduct.id,{data:this.tempProduct}).then((res)=>{
          if (res.data.success){
             $("#productModal").modal("hide");
             this.getProducts();
          }else{
             console.log("")
          }
        })
        .catch(err=>console.log(err))
      }

    },

    deleteProduct(){
      deleteBsProductAPI(this.tempProduct.id).then((res)=>{
        if(res.data.success){
          $("#delProductModal").modal("hide");
          this.getProducts();
        }else{
          console.log(response.data);
        }

      })
    },

    uploadFile(){
      console.log(this);
      //
      const uploadedFile =this.$refs.files.files[0];
      //
      const formData= new FormData();
      //formData
      const vm=this;
      formData.append("file-to-upload",uploadedFile);
      //--"",
      const api=`${process.env.APIPATH}api/${process.env.CUSTOMPATH}/admin/upload`;
      vm.status.fileUploading=true;
      this.$http.post(api,formData,{
          headers: {
      'Content-Type': 'multipart/form-data' // FormData 
      }
      }).then((response)=>{
          vm.status.fileUploading=false;
          console.log(response.data);
          if(response.data.success){
              vm.tempProduct.imageUrl=response.data.imageUrl;
          }
      })
      //formDataAPI
      //

    }
    },
    created() {
     this.getProducts();
 },

}
</script>

<style scoped>


</style>



